import * as THREE from 'three';
import { Constants as BaseConstants } from 'chessboard/renderers/webgl/base/constants';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { rotateAroundAxis } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Add the analysis overlay.
 */
export function addAnalysisOverlay(state: WebGLRenderer3D.State): void {
  const { scene, squareSize } = state;

  if (state.analysisOverlay) {
    return;
  }

  const overlay = new THREE.Mesh(
    new THREE.PlaneBufferGeometry(Constants.Board.Size, Constants.Board.Size, 1, 1),
    new THREE.MeshBasicMaterial({
      color: 0xffffff,
      opacity: BaseConstants.AnalysisOverlay.Opacity,
      transparent: true,
      depthWrite: false,
    }),
  );

  overlay.position.set(squareSize * 4, 0, squareSize * 4);
  overlay.renderOrder = Constants.ObjectsRenderOrder.AnalysisOverlay;

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: overlay,
  });

  scene.add(overlay);

  state.analysisOverlay = overlay;
}
