import * as THREE from 'three';
import { Constants as BaseConstants } from 'chessboard/renderers/webgl/base/constants';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import { EffectIcons } from 'chessboard/plugins/effects/renderer/icons';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { makeSvgIcon } from 'chessboard/plugins/effects/helpers/make-svg-icon';
import { rotateAroundAxis, textureFromSVG } from 'chessboard/renderers/webgl/three-d/utils';
import { updateEffectPlacement } from 'chessboard/renderers/webgl/three-d/effects';

/**
 * Create the visual object for an effect.
 */
export function createEffectObj(
  state: WebGLRenderer3D.State,
  effectInfo: WebGLRenderer.Effect.Info,
): THREE.Mesh[] {
  const { squareSize } = state;
  const { position, type } = effectInfo;

  const svgMarkup = makeSvgIcon({
    path: EffectIcons[<keyof typeof EffectIcons>type],
    size: '100%',
    viewBox: '0 0 19 19',
  });

  const material = new THREE.MeshBasicMaterial({
    depthTest: false,
    depthWrite: false,
    transparent: true,
    map: textureFromSVG(svgMarkup),
  });

  const size = squareSize * BaseConstants.Effect.SizeRatio;

  const geometry = new THREE.PlaneBufferGeometry(size, size, 1, 1);
  const mesh = new THREE.Mesh(geometry, material);

  mesh.renderOrder = Constants.ObjectsRenderOrder.Effect;

  updateEffectPlacement(state, { effectObj: mesh, position });

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: mesh,
  });

  return [mesh];
}
