import type { Animator } from 'chessboard/renderers/model/animator/types';
import { Constants } from 'chessboard/renderers/webgl/three-d/animations/default/config/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { easeOutQuadratic } from 'chessboard/renderers/model/animator/easing';

/**
 * Hide hint with animation.
 */
export function hideHint(
  state: WebGLRenderer3D.State,
  hint: WebGLRenderer3D.Hint.Model,
): Animator.Animation.Model {
  return state.animator.play({
    duration: Constants.Hint.Duration,
    easing: easeOutQuadratic,
    keyframes: {
      0: {
        x: 1,
        y: 1,
      },
      100: {
        x: 0.0001,
        y: 0.0001,
      },
    },
    target: hint.obj.scale,
  });
}
