import * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { GLTF } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { ObjectType } from 'chessboard/renderers/webgl/three-d/types';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { getObjectsToLoad } from 'chessboard/renderers/webgl/three-d/main/get-objects-to-load';
import { rotateAroundAxis } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Load all objects.
 */
export function loadObjects(state: WebGLRenderer3D.State): Promise<void> {
  return new Promise((resolve) => {
    const {
      lights,
      materials,
      piecesGeom,
      scene,
      options: { themeAssets },
      pieceStyle,
    } = state;
    let loadedObjectsCount = 0;

    const objectsToLoad = getObjectsToLoad(pieceStyle, themeAssets);

    objectsToLoad.forEach((item) => {
      const gltfLoader = new GLTFLoader();

      gltfLoader.load(item.url, (data: GLTF) => {
        if (state.destroyed) {
          return;
        }

        loadedObjectsCount += 1;

        const geom = (<THREE.Mesh>data.scene.children[0]).geometry;

        if (item.type === ObjectType.Board) {
          const boardModel = new THREE.Mesh(geom, materials.board);

          rotateAroundAxis({
            axis: new THREE.Vector3(1, 0, 0),
            degrees: 90,
            object: boardModel,
          });

          boardModel.position.x = Constants.Square.Size * 4;
          boardModel.position.z = Constants.Square.Size * 4;

          (<THREE.DirectionalLight>lights.whiteSideLight).target = boardModel;
          (<THREE.DirectionalLight>lights.blackSideLight).target = boardModel;

          scene.add(boardModel);

          state.boardModel = boardModel;
        } else if (item.type === ObjectType.Piece) {
          piecesGeom[item.name] = geom;
        }

        if (loadedObjectsCount === objectsToLoad.length) {
          resolve();
        }
      });
    });
  });
}
