import type { Board } from 'chessboard/model/board/types';
import { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Normalize the mouse coordinates.
 */
export function normalizePointer(
  _state: WebGLRenderer3D.State,
  coords: Board.Coords,
): Board.Coords {
  coords.x *= WebGLRenderer.PixelRatio;
  coords.y *= WebGLRenderer.PixelRatio;

  return coords;
}
