import { Game } from 'chessboard/model/game/types';
import type { Material, WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Make a piece semi-transparent.
 */
export function fadePiece({
  materials,
  piece,
  shouldFade,
}: {
  materials: Material.Collection;
  piece: WebGLRenderer3D.Piece.Model;
  shouldFade: boolean;
}): void {
  const pieceMesh = piece.obj.children[0] as THREE.Mesh;

  // hide/show shadow
  piece.obj.children[1].visible = !shouldFade;

  if (shouldFade) {
    if (piece.info.color === Game.ColorsAsNumbers.White) {
      pieceMesh.material = materials.whitePieceOverlapped;
    } else {
      pieceMesh.material = materials.blackPieceOverlapped;
    }

    return;
  }

  if (pieceMesh.material === materials.whitePieceOverlapped) {
    pieceMesh.material = materials.whitePiece;
  } else if (pieceMesh.material === materials.blackPieceOverlapped) {
    pieceMesh.material = materials.blackPiece;
  }
}
