import * as THREE from 'three';
import type { CameraState } from 'chessboard/renderers/webgl/three-d/types';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import { getCameraSettings } from 'chessboard/renderers/webgl/three-d/camera/get-camera-settings';

/**
 * Create the camera state.
 */
export function createCameraState(baseState: WebGLRenderer.State): CameraState {
  const cameraSettings = getCameraSettings();

  return {
    animations: {
      panning: null,
      rotation: null,
    },
    animator: baseState.animator,
    camera: new THREE.PerspectiveCamera(35, 1, 1, 2000),
    distance: cameraSettings.distance,
    horizontalPanning: cameraSettings.horizontalPanning,
    isPanning: false,
    isRotating: false,
    panAngle: cameraSettings.panAngle,
    panAngleFlipped: 0,
    target: new THREE.Vector3(cameraSettings.target.x, 0, cameraSettings.target.z),
    tiltAngle: cameraSettings.tiltAngle,
    verticalPanning: cameraSettings.verticalPanning,
  };
}
