import * as THREE from 'three';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { getWebglAssetUrl } from 'src/utils/get-webgl-asset-url';
import { defaultLegacyBoardStyles } from 'chessboard/config/legacy-themes';

/**
 * Change board texture.
 */
export function updateBoardStyle(state: WebGLRenderer3D.State): Promise<void> {
  return new Promise((resolve) => {
    const {
      boardThemes,
      boardStyle,
      options: { themeAssets },
    } = state;

    const textureLoader = new THREE.TextureLoader();

    const theme = boardThemes[boardStyle] ?? defaultLegacyBoardStyles.green;
    const boardStylePath = theme[2];
    const boardTextureUrl =
      themeAssets?.board.assets.background ||
      getWebglAssetUrl(`three-d/board/textures/board_${boardStylePath}.jpg`);
    const boardNotationTextureUrl =
      themeAssets?.board.assets.coordinates ||
      getWebglAssetUrl(`three-d/board/textures/board_${boardStylePath}_notation.png`);

    // load board texture
    textureLoader.load(boardTextureUrl, (boardTexture) => {
      boardTexture.flipY = false;
      (state.materials.board as THREE.MeshLambertMaterial).map = boardTexture;

      // load board notation texture
      textureLoader.load(boardNotationTextureUrl, (boardNotationTexture) => {
        (state.materials.boardNotation as THREE.MeshLambertMaterial).map = boardNotationTexture;

        resolve();
      });
    });
  });
}
