import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { rotateCamera, saveCameraSettings } from 'chessboard/renderers/webgl/three-d/camera';

/**
 * On mouse wheel.
 */
export function onMouseWheel(state: WebGLRenderer3D.State, wheelDelta: number): boolean {
  const { cameraState, isPromotionWindowVisible, options, pointer, viewEl } = state;

  if (isPromotionWindowVisible || pointer.event.target !== viewEl) {
    return false;
  }

  // zoom camera
  if (wheelDelta > 0) {
    if (cameraState.distance > Constants.Camera.DistanceMax) {
      return true;
    }

    cameraState.distance += Constants.Camera.ZoomIncrement;
  } else {
    if (cameraState.distance < Constants.Camera.DistanceMin) {
      return true;
    }

    cameraState.distance -= Constants.Camera.ZoomIncrement;
  }

  rotateCamera({
    panAngle: cameraState.panAngle,
    state: cameraState,
    tiltAngle: cameraState.tiltAngle,
  });

  saveCameraSettings({ isFlipped: options.flipped, state: cameraState });

  return true;
}
