import type { Coords3D, WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { getPieceFromSanSquare, worldToSanSquare } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Get the piece under a mouse position.
 */
export function getPieceFromMousePoint(
  coords: Coords3D,
  pieces: WebGLRenderer3D.Piece.Collection,
): WebGLRenderer3D.Piece.Model | null {
  const square = worldToSanSquare(coords);

  if (square) {
    return getPieceFromSanSquare(square, pieces);
  }

  return null;
}
