import type { CameraSettings } from 'chessboard/renderers/webgl/three-d/types';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';

/**
 * Retrieves camera settings from localStorage.
 */
export function getCameraSettings(): CameraSettings {
  const settingsSerialized = localStorage.getItem(Constants.CacheKeys.CameraSettings);
  const settings = settingsSerialized ? JSON.parse(settingsSerialized) : null;

  if (!settings) {
    return {
      distance: Constants.Camera.Distance,
      horizontalPanning: Constants.Camera.HorizontalPanning,
      panAngle: Constants.Camera.PanAngle,
      target: {
        x: Constants.Square.Size * 4,
        y: 0,
        z: Constants.Square.Size * 4,
      },
      tiltAngle: Constants.Camera.TiltAngle,
      verticalPanning: Constants.Camera.VerticalPanning,
    };
  }

  return settings;
}
