import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { findPieceFromPosition } from 'chessboard/renderers/webgl/three-d/pieces/find-piece-from-position';
import { rotateCamera, panCamera } from 'chessboard/renderers/webgl/three-d/camera';

/**
 * On pointer move.
 */
export function onPointerMove(state: WebGLRenderer3D.State): void {
  const { containerEl, isPromotionWindowVisible, pointer, selectedPiece } = state;

  if (isPromotionWindowVisible) {
    return;
  }

  doCameraManipulation(state);

  if (!state.isBoardEnabled) {
    return;
  }

  if (!selectedPiece && findPieceFromPosition(state, pointer.boardPosition)) {
    containerEl.classList.add(Constants.CssClasses.PieceHover);
  } else if (!selectedPiece) {
    containerEl.classList.remove(Constants.CssClasses.PieceHover);
  }
}

/**
 * Manipulate camera based on user input.
 */
function doCameraManipulation(state: WebGLRenderer3D.State): void {
  const { pointer } = state;

  if (!pointer.down || pointer.downInsideBoard) {
    return;
  }

  if (pointer.rightDown) {
    doCameraRotation(state);
  } else if (pointer.secondaryDown) {
    doCameraPan(state);
  }
}

/**
 * Rotate camera based on user input.
 */
function doCameraRotation(state: WebGLRenderer3D.State): void {
  const { cameraState, pointer } = state;
  const { viewPosition, previousViewPosition } = pointer;

  const cameraPanAngle = cameraState.panAngle - (viewPosition.x - previousViewPosition.x) * 0.6;

  const cameraTiltAngle = cameraState.tiltAngle + (viewPosition.y - previousViewPosition.y) * 0.6;

  rotateCamera({
    panAngle: cameraPanAngle,
    state: cameraState,
    tiltAngle: cameraTiltAngle,
  });

  cameraState.isRotating = true;
}

/**
 * Pan camera based on user input.
 */
function doCameraPan(state: WebGLRenderer3D.State): void {
  const { cameraState, pointer } = state;
  const { viewPosition, previousViewPosition } = pointer;

  const cameraHorizontalPanning = (viewPosition.x - previousViewPosition.x) * 0.1 * -1;
  const cameraVerticalPanning = (viewPosition.y - previousViewPosition.y) * 0.1;

  panCamera({
    panH: cameraHorizontalPanning,
    panV: cameraVerticalPanning,
    state: cameraState,
  });

  cameraState.isPanning = true;
}
