import type { Texture, WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { createBoardMaterials } from 'chessboard/renderers/webgl/three-d/materials/create-board-materials';
import { createPieceMaterials } from 'chessboard/renderers/webgl/three-d/materials/create-piece-materials';
import { createShadowMaterials } from 'chessboard/renderers/webgl/three-d/materials/create-shadow-materials';

/**
 * Create the materials.
 */
export function createMaterials(state: WebGLRenderer3D.State, textures: Texture.Collection) {
  Object.assign(state.materials, createBoardMaterials(textures));
  Object.assign(state.materials, createPieceMaterials(textures));
  Object.assign(state.shadowMaterials, createShadowMaterials(textures));
}
