import * as THREE from 'three';
import { Board as BoardTypes } from 'chessboard/model/board/types';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { rotateAroundAxis } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create the board notation plane.
 */
export function createBoardNotationPlane(state: WebGLRenderer3D.State): void {
  const boardNotationGeometry = new THREE.PlaneBufferGeometry(
    Constants.Board.Size,
    Constants.Board.Size,
    1,
    1,
  );
  state.boardNotationPlane = new THREE.Mesh(boardNotationGeometry);
  state.boardNotationPlane.position.set(Constants.Square.Size * 4, 0, Constants.Square.Size * 4);
  state.boardNotationPlane.renderOrder = Constants.ObjectsRenderOrder.NotationPlane;

  if (state.options.coordinates === BoardTypes.CoordinatesPositions.Off) {
    state.boardNotationPlane.visible = false;
  }

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: state.boardNotationPlane,
  });

  state.scene.add(state.boardNotationPlane);
}
