import type { WebGLRenderer3D, Texture } from 'chessboard/renderers/webgl/three-d/types';
import { createMaterials } from 'chessboard/renderers/webgl/three-d/materials';
import { loadObjects } from 'chessboard/renderers/webgl/three-d/main/load-objects';
import { loadTextures } from 'chessboard/renderers/webgl/three-d/main/load-textures';

/**
 * Load all assets for the board.
 */
export function loadAssets(state: WebGLRenderer3D.State): Promise<void> {
  return new Promise((resolve) => {
    loadTextures(state).then((textures: Texture.Collection) => {
      if (state.destroyed) {
        return;
      }

      createMaterials(state, textures);

      (state.boardNotationPlane as THREE.Mesh).material = state.materials.boardNotation;
      (state.groundModel as THREE.Mesh).material = state.shadowMaterials.ground;
      (state.hoverSquare as THREE.Mesh).material = state.materials.squareOver;

      loadObjects(state).then(() => {
        resolve();
      });
    });
  });
}
