import type { Game } from 'chessboard/model/game/types';
import { Pointerdown } from 'chessboard/model/dom/get-pointer-events';
import { ChessError } from 'chessboard/model/error';
import { ErrorCodes } from 'chessboard/model/error/types';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { getPromotionWindowCloseButtonId } from 'chessboard/renderers/webgl/three-d/promotion-window/get-promotion-window-identifiers';

/**
 * Initialize the promotion window.
 */
export function initPromotionWindow(state: WebGLRenderer3D.State): void {
  const closeBtnEl = document.getElementById(`${getPromotionWindowCloseButtonId(state.boardId)}`);

  if (!state.promotionWindowEl) {
    throw new ChessError({
      code: ErrorCodes.ElementNotFound,
      message: 'Promotion window does not exist.',
    });
  }

  if (!closeBtnEl) {
    throw new ChessError({
      code: ErrorCodes.ElementNotFound,
      message: 'Promotion window close button does not exist.',
    });
  }

  // listen for piece selection
  state.promotionWindowEl.addEventListener(Pointerdown, (event) => {
    event.preventDefault();
    event.stopPropagation();

    const pieceType = (event.target as HTMLElement).getAttribute('data-pieceType');

    if (pieceType) {
      state.promotionHandler(<Game.Piece.Types>pieceType);
    }

    return false;
  });

  // listen for close button
  closeBtnEl.addEventListener(Pointerdown, (event) => {
    event.preventDefault();
    event.stopPropagation();

    state.promotionHandler();

    return false;
  });

  state.isPromotionWindowInitialized = true;
}
