import * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { rotateAroundAxis } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create the ground plane.
 */
export function createGroundPlane(state: WebGLRenderer3D.State): void {
  state.groundModel = new THREE.Mesh(new THREE.PlaneBufferGeometry(110, 110, 1, 1));
  state.groundModel.position.set(Constants.Square.Size * 4, -2, Constants.Square.Size * 4);

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: state.groundModel,
  });

  state.scene.add(state.groundModel);
}
