import { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { initCamera } from 'chessboard/renderers/webgl/three-d/camera/init-camera';
import {
  createBoardNotationPlane,
  createGroundPlane,
} from 'chessboard/renderers/webgl/three-d/board';
import { createLights } from 'chessboard/renderers/webgl/three-d/main/create-lights';
import { createPromotionWindow } from 'chessboard/renderers/webgl/three-d/promotion-window/create-promotion-window';

/**
 * Initialize the 3D board renderer.
 */
export function init(state: WebGLRenderer3D.State): void {
  state.renderer.setPixelRatio(WebGLRenderer.PixelRatio);
  state.scene.add(state.cameraState.camera);

  initCamera({
    isFlipped: state.isBoardFlipped,
    state: state.cameraState,
  });

  createLights(state);

  createBoardNotationPlane(state);
  createGroundPlane(state);

  createPromotionWindow(state);
}
