import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Remove the analysis overlay.
 */
export function removeAnalysisOverlay(state: WebGLRenderer3D.State): void {
  const { scene } = state;

  if (state.analysisOverlay) {
    scene.remove(state.analysisOverlay);
  }

  state.analysisOverlay = null;
}
