import * as THREE from 'three';
import type { Board } from 'chessboard/model/board/types';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Converts the board position to view coordinates.
 */
export function getViewPosition(
  state: WebGLRenderer3D.State,
  position: WebGLRenderer.BoardPosition,
): Board.Coords {
  const { cameraState, viewEl } = state;
  const { camera } = cameraState;

  const worldPos = new THREE.Vector3(position.x, 0, position.y);
  const widthHalf = viewEl.width / 2;
  const heightHalf = viewEl.height / 2;

  worldPos.project(camera);
  worldPos.x = worldPos.x * widthHalf + widthHalf;
  worldPos.y = -worldPos.y * heightHalf + heightHalf;

  return {
    x: worldPos.x,
    y: worldPos.y,
  };
}
