import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { createRenderer } from 'chessboard/renderers/webgl/three-d/main';

/**
 * Handle WebGL context lost.
 */
export function onWebGLContextLost(state: WebGLRenderer3D.State): void {
  const { viewSize, viewEl } = state;
  const renderer = createRenderer(viewEl);

  renderer.setSize(viewSize.width, viewSize.height);

  state.renderer = renderer;
}
