import * as THREE from 'three';
import { Game } from 'chessboard/model/game/types';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Updates the visual representation of a piece.
 */
export function updatePieceVisuals(
  state: WebGLRenderer3D.State,
  piece: WebGLRenderer3D.Piece.Model,
): void {
  const { piecesGeom, materials, shadowMaterials } = state;

  const bodyMesh = <THREE.Mesh>piece.obj.children[0];
  const shadowPlane = <THREE.Mesh>piece.obj.children[1];

  bodyMesh.geometry = piecesGeom[Game.Piece.Names[piece.info.type]];
  bodyMesh.material =
    piece.info.color === Game.ColorsAsNumbers.White ? materials.whitePiece : materials.blackPiece;

  shadowPlane.material = shadowMaterials[Game.Piece.Names[piece.info.type]];

  if (piece.info.type === Game.Piece.Types.Knight) {
    bodyMesh.rotation.z = THREE.MathUtils.degToRad(
      piece.info.color === Game.ColorsAsNumbers.Black ? -90 : 90,
    );
  } else {
    bodyMesh.rotation.z = 0;
  }
}
