import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Remove an arrow from the board.
 */
export function removeArrow(
  state: WebGLRenderer3D.State,
  arrow: WebGLRenderer3D.Arrow.Model,
): void {
  state.scene.remove(arrow.obj);

  arrow.obj.geometry.dispose();
}
