import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { rotateCamera } from 'chessboard/renderers/webgl/three-d/camera';
import { updateEffectPlacement } from 'chessboard/renderers/webgl/three-d/effects';

/**
 * Flips the board.
 */
export function flipBoard(state: WebGLRenderer3D.State, shouldFlip: boolean): void {
  const panAngleToSubtract = state.isBoardFlipped ? 360 : -360;

  state.cameraState.panAngleFlipped = state.isBoardFlipping
    ? state.cameraState.panAngleFlipped + panAngleToSubtract
    : state.cameraState.panAngle + panAngleToSubtract;

  state.isBoardFlipped = shouldFlip;
  state.isBoardFlipping = true;

  rotateCamera({
    animation: state.animation,
    panAngle: state.cameraState.panAngleFlipped,
    state: state.cameraState,
    tiltAngle: state.cameraState.tiltAngle,
  }).then(() => {
    state.isBoardFlipping = false;
  });

  Object.values(state.effects).forEach((effect) => {
    effect.obj.forEach((obj) => {
      updateEffectPlacement(state, { effectObj: obj, position: effect.info.position });
    });
  });
}
