import * as THREE from 'three';
import { Constants as BaseConstants } from 'chessboard/renderers/webgl/base/constants';
import { listenForImageFailure } from 'chessboard/renderers/webgl/three-d/utils/listen-for-image-failure';

/**
 * Create a texture from SVG markup.
 */
export function textureFromSVG(svgMarkup: string, transparent: boolean = true): THREE.Texture {
  const img = document.createElement('img');

  img.src = `data:image/svg+xml;utf8,${encodeURIComponent(svgMarkup)}`;
  img.width = BaseConstants.SVG.ImageSize;
  img.height = BaseConstants.SVG.ImageSize;

  listenForImageFailure(img);

  const texture = new THREE.Texture(img);

  if (transparent) {
    // fix dark border around transparent textures
    texture.minFilter = THREE.NearestFilter;
  }

  texture.needsUpdate = true;

  return texture;
}
