import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { findPieceFromPosition } from 'chessboard/renderers/webgl/three-d/pieces/find-piece-from-position';

/**
 * On pointer down.
 */
export function onPointerDown(state: WebGLRenderer3D.State): void {
  const { containerEl, isPromotionWindowVisible, pointer } = state;

  if (isPromotionWindowVisible) {
    return;
  }

  if (findPieceFromPosition(state, pointer.boardPosition)) {
    containerEl.classList.add(Constants.CssClasses.PieceDrag);
  }
}
