import type * as THREE from 'three';

/**
 * Rotate an object along an axis in object space using degrees.
 */
export function rotateAroundAxis({
  axis,
  degrees,
  object,
}: {
  axis: THREE.Vector3;
  degrees: number;
  object: THREE.Object3D;
}): void {
  const radians = (degrees * Math.PI) / 180;

  object.rotateOnAxis(axis, radians);
}
