import type * as THREE from 'three';
import { Constants as BaseConstants } from 'chessboard/renderers/webgl/base/constants';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Update the effect's placement on the board.
 */
export function updateEffectPlacement(
  state: WebGLRenderer3D.State,
  {
    effectObj,
    position,
  }: {
    effectObj: THREE.Mesh;
    position: WebGLRenderer.BoardPosition;
  },
): void {
  const { squareSize, isBoardFlipped } = state;

  const flipMultiplier = isBoardFlipped ? -1 : 1;
  const offset = squareSize * BaseConstants.Effect.OffsetRatio * flipMultiplier;

  effectObj.position.set(position.x + offset, Constants.Effect.PositionY, position.y - offset);

  effectObj.scale.x = isBoardFlipped ? -1 : 1;
  effectObj.scale.y = isBoardFlipped ? -1 : 1;
}
