import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { getImportMetaEnvDev } from 'src/utils/import-meta-wrappers';

/**
 * Render the scene.
 */
export function render(state: WebGLRenderer3D.State): void {
  state.lights.movingLight.position.x = state.cameraState.camera.position.x;
  state.lights.movingLight.position.z = state.cameraState.camera.position.z;

  state.renderer.render(state.scene, state.cameraState.camera);

  if (getImportMetaEnvDev()) {
    showStatistics(state);
  }
}

/**
 * Display statistics information.
 */
function showStatistics(state: WebGLRenderer3D.State): void {
  /* eslint-disable @typescript-eslint/no-non-null-assertion */
  const { containerEl, renderer } = state;
  const { info } = renderer;

  let panel = containerEl.querySelector(`.${Constants.CssClasses.StatisticsPanel}`);

  if (!panel) {
    panel = document.createElement('div');
    panel.className = Constants.CssClasses.StatisticsPanel;

    panel.innerHTML = `
      <div class="group">
        <div class="item">Frame: <span class="frame"></span></div>
        <div class="item">Draw Calls: <span class="draw-calls"></span></div>
        <div class="item">Lines: <span class="lines"></span></div>
        <div class="item">Points: <span class="points"></span></div>
        <div class="item">Triangles: <span class="triangles"></span></div>
      </div>

      <div class="separator"></div>

      <div class="group">
        <div class="item">Geometries: <span class="geometries"></span></div>
        <div class="item">Textures: <span class="textures"></span></div>
        <div class="item">Programs: <span class="programs"></span></div>
      </div>

      <div class="separator"></div>
    `;

    containerEl.appendChild(panel);
  }

  panel.querySelector('.frame')!.innerHTML = info.render.frame.toString();
  panel.querySelector('.draw-calls')!.innerHTML = info.render.calls.toString();
  panel.querySelector('.lines')!.innerHTML = info.render.lines.toString();
  panel.querySelector('.points')!.innerHTML = info.render.points.toString();
  panel.querySelector('.triangles')!.innerHTML = info.render.triangles.toString();

  panel.querySelector('.geometries')!.innerHTML = info.memory.geometries.toString();
  panel.querySelector('.textures')!.innerHTML = info.memory.textures.toString();
  panel.querySelector('.programs')!.innerHTML = info.programs?.length.toString() || '0';
}
