import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Load the pieces' textures.
 */
export function updatePiecesStyle(state: WebGLRenderer3D.State): Promise<void> {
  // todo https://chesscom.atlassian.net/browse/CV-309807
  state.pieceStyle = Constants.PieceStyles.Default;

  return new Promise((resolve) => resolve());
}
