import { getColorAsWord } from 'chessboard/model/game/get-color-as-word';
import type { Game } from 'model/game/types';

/**
 * Get the base id for the promotion window.
 */
export function getPromotionWindowBaseId(boardId: string): string {
  return `${boardId}_promotionWindow3D`;
}

/**
 * Get the id for the close button of the promotion window.
 */
export function getPromotionWindowCloseButtonId(boardId: string): string {
  return `${getPromotionWindowBaseId(boardId)}_closeButton`;
}

/**
 * Get the id for the pieces container of the promotion window.
 */
export function getPromotionWindowPiecesContainerId(
  boardId: string,
  color: Game.ColorsAsNumbers,
): string {
  return `${getPromotionWindowBaseId(boardId)}_${getColorAsWord(color)}`;
}
