import type * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { animateTexture } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Remove a custom item from the board.
 */
export function removeCustomItem(
  state: WebGLRenderer3D.State,
  item: WebGLRenderer3D.CustomItem.Model,
): void {
  const material = <THREE.MeshBasicMaterial>item.obj.material;
  const texture = material.map;

  if (item.info.frame?.imageAnimated && texture) {
    // render on top of everything
    material.depthTest = false;
    item.obj.renderOrder = Constants.ObjectsRenderOrder.Highest;

    animateTexture(texture, {
      animator: state.animator,
      onEnd: () => {
        state.scene.remove(item.obj);
      },
    });
  } else {
    state.scene.remove(item.obj);
  }
}
