import type * as THREE from 'three';
import type { Animator } from 'chessboard/renderers/model/animator/types';
import { Constants } from 'chessboard/renderers/webgl/three-d/animations/default/config/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Hide custom item with animation.
 */
export function hideCustomItem(
  state: WebGLRenderer3D.State,
  item: WebGLRenderer3D.CustomItem.Model,
): Animator.Animation.Model {
  return state.animator.play({
    data: {
      itemObj: item.obj,
    },
    duration: Constants.CustomItem.Duration,
    keyframes: {
      0: {
        opacity: (<THREE.MeshBasicMaterial>item.obj.material).opacity,
        scale: 1,
      },
      100: {
        opacity: 0,
        scale: 2,
      },
    },
    onUpdate: onHideCustomItemUpdate,
  });
}

/**
 * Updates custom item every frame.
 */
export function onHideCustomItemUpdate(animation: Animator.Animation.Model): void {
  const { animatedProps, data } = animation;
  const itemObj = <THREE.Mesh>data.itemObj;

  const opacity = animatedProps.opacity;
  const scale = animatedProps.scale;

  itemObj.scale.x = scale;
  itemObj.scale.y = scale;
  itemObj.scale.z = scale;

  (<THREE.MeshBasicMaterial>itemObj.material).opacity = opacity;
}
