import type { CameraState } from 'chessboard/renderers/webgl/three-d/types';
import { rotateCamera, panCamera } from 'chessboard/renderers/webgl/three-d/camera';

/**
 * Initialize the camera.
 */
export function initCamera({ state, isFlipped }: { state: CameraState; isFlipped: boolean }): void {
  if (isFlipped) {
    state.panAngle += 360;
  }

  rotateCamera({
    panAngle: state.panAngle,
    state,
    tiltAngle: state.tiltAngle,
  });

  panCamera({
    panH: state.horizontalPanning,
    panV: state.verticalPanning,
    state,
  });
}
