import type { CameraState } from 'chessboard/renderers/webgl/three-d/types';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';

/**
 * Saves camera settings to localStorage.
 */
export function saveCameraSettings({
  state,
  isFlipped,
  useDefault,
}: {
  state: CameraState;
  isFlipped: boolean;
  useDefault?: boolean;
}): void {
  let cameraSettings = {
    distance: state.distance,
    horizontalPanning: state.horizontalPanning,
    panAngle: isFlipped ? state.panAngle - 360 : state.panAngle,
    target: {
      x: state.target.x,
      y: state.target.y,
      z: state.target.z,
    },
    tiltAngle: state.tiltAngle,
    verticalPanning: state.verticalPanning,
  };

  if (useDefault) {
    cameraSettings = {
      distance: Constants.Camera.Distance,
      horizontalPanning: Constants.Camera.HorizontalPanning,
      panAngle: Constants.Camera.PanAngle,
      target: {
        x: state.target.x,
        y: 0,
        z: state.target.z,
      },
      tiltAngle: Constants.Camera.TiltAngle,
      verticalPanning: Constants.Camera.VerticalPanning,
    };
  }

  localStorage.setItem(Constants.CacheKeys.CameraSettings, JSON.stringify(cameraSettings));
}
