import type * as THREE from 'three';
import { loadTexture } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create a texture from an animated image URL with all its frames in a single row.
 */
export function loadAnimatedTexture(
  imageURL: string,
  {
    transparent = false,
  }: {
    transparent?: boolean;
  } = {},
): Promise<THREE.Texture> {
  return new Promise((resolve) => {
    loadTexture(imageURL, { transparent }).then((texture) => {
      const image = texture.image;
      const framesCount = Math.ceil(image.width / image.height);

      texture.repeat.x = 1 / framesCount;

      resolve(texture);
    });
  });
}
