import * as THREE from 'three';
import * as DefaultAnimation from 'chessboard/renderers/webgl/three-d/animations/default';
import { Constants as BaseConstants } from 'chessboard/renderers/webgl/base/constants';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { createCameraState } from 'chessboard/renderers/webgl/three-d/camera';
import { createRenderer } from 'chessboard/renderers/webgl/three-d/main';
import { validateBoardOptions3D } from 'chessboard/config/theme-validation-utils';

/**
 * Create the 3D board state.
 */
export function createState(baseState: WebGLRenderer.State): WebGLRenderer3D.State {
  return {
    ...baseState,
    options: validateBoardOptions3D(baseState.options),
    analysisOverlay: null,
    animation: DefaultAnimation,
    boardModel: null,
    boardNotationPlane: null,
    cameraState: createCameraState(baseState),
    captureHintGeom: new THREE.RingGeometry(
      Constants.Square.Size * BaseConstants.CaptureHint.InnerRadiusRatio,
      Constants.Square.Size * BaseConstants.CaptureHint.OuterRadiusRatio,
      25,
      1,
    ),
    customItemGeom: new THREE.PlaneBufferGeometry(
      Constants.Square.Size,
      Constants.Square.Size,
      1,
      1,
    ),
    groundModel: null,
    highlightGeom: new THREE.PlaneBufferGeometry(
      Constants.Square.Size,
      Constants.Square.Size,
      1,
      1,
    ),
    isBoardFlipping: false,
    isPromotionWindowInitialized: false,
    lights: {},
    materials: {},
    moveHintGeom: new THREE.CircleGeometry(
      Constants.Square.Size * BaseConstants.MoveHint.RadiusRatio,
      16,
    ),
    overlappedPiece: null,
    piecesGeom: {},
    pieceStyle: Constants.PieceStyles.Default,
    promotionHandler: () => {},
    renderer: createRenderer(baseState.viewEl),
    scene: new THREE.Scene(),
    shadowMaterials: {},
    squareSize: Constants.Square.Size,
  };
}
