import * as THREE from 'three';
import type { CameraState } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Pan the camera horizontally.
 */
export function panCameraHorizontally(state: CameraState, panDistance: number): void {
  const panOffset = calculatePanOffset({
    cameraPosition: state.camera.position,
    cameraTarget: state.target,
    panDistance,
  });

  state.camera.position.add(panOffset);
  state.target.add(panOffset);

  state.horizontalPanning = panDistance;
}

/**
 * Calculate camera position offset for panning horizontally.
 */
function calculatePanOffset({
  cameraPosition,
  cameraTarget,
  panDistance,
}: {
  cameraPosition: THREE.Vector3;
  cameraTarget: THREE.Vector3;
  panDistance: number;
}): THREE.Vector3 {
  const cameraDirection = cameraTarget.clone().sub(cameraPosition);

  const panDirection = cameraDirection.clone().cross(THREE.Object3D.DefaultUp).normalize();

  const panOffset = panDirection.clone().multiplyScalar(panDistance);

  return new THREE.Vector3(panOffset.x, 0, panOffset.z);
}
