import type * as THREE from 'three';
import type { Board as BoardTypes } from 'chessboard/model/board/types';
import { getSanFromFileRank } from 'chessboard/model/board/get-san-from-file-rank';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { Coords3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Converts the world position to a SAN square.
 */
export function worldToSanSquare(pos: THREE.Vector3 | Coords3D): BoardTypes.SanSquare | null {
  const file = Math.ceil(pos.x / Constants.Square.Size);
  const rank = Math.ceil((Constants.Square.Size * 8 - pos.z) / Constants.Square.Size);

  return getSanFromFileRank({ file, rank });
}
