import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { AnimationHandlers, CameraState } from 'chessboard/renderers/webgl/three-d/types';
import { rotateCamera, saveCameraSettings } from 'chessboard/renderers/webgl/three-d/camera';

/**
 * Reset the camera.
 */
export function resetCamera({
  state,
  isFlipped,
  animation,
}: {
  state: CameraState;
  isFlipped: boolean;
  animation?: AnimationHandlers;
}): void {
  state.horizontalPanning = Constants.Camera.HorizontalPanning;
  state.verticalPanning = Constants.Camera.VerticalPanning;

  state.target.x = Constants.Square.Size * 4;
  state.target.z = Constants.Square.Size * 4;
  state.target.y = Constants.Camera.VerticalPanning;

  if (isFlipped) {
    rotateCamera({
      animation,
      panAngle: Constants.Camera.PanAngle + 360,
      state,
      tiltAngle: Constants.Camera.TiltAngle,
    });
  } else {
    rotateCamera({
      animation,
      panAngle: Constants.Camera.PanAngle,
      state,
      tiltAngle: Constants.Camera.TiltAngle,
    });
  }

  if (animation) {
    animation.changeCameraDistance(state, Constants.Camera.Distance);
  } else {
    state.distance = Constants.Camera.Distance;
  }

  saveCameraSettings({ isFlipped, state, useDefault: true });
}
