/**
 * dynamically assign a failed property to the img on failure to detect in tests
 * https://github.com/whatwg/html/issues/3510
 * https://stackoverflow.com/questions/28311991/how-to-verify-an-svg-img-loaded-properly-in-firefox-in-a-webdriver-test
 */
export function listenForImageFailure(img: HTMLImageElement | null) {
  if (!img) {
    return;
  }

  img.onerror = function () {
    (this as HTMLImageElement & { 'test-failed'?: boolean })['test-failed'] = true;
  };
}
