import { Board } from 'chessboard/model/board/types';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Update board coordinates.
 */
export function updateBoardCoordinates(
  state: WebGLRenderer3D.State,
  {
    position,
  }: {
    position: Board.CoordinatesPositions;
  },
): void {
  if (!state.boardNotationPlane) {
    return;
  }

  state.boardNotationPlane.visible = position !== Board.CoordinatesPositions.Off;
}
