import * as THREE from 'three';
import type { Texture } from 'chessboard/renderers/webgl/three-d/types';
import { Material } from 'chessboard/renderers/webgl/three-d/types';
import { createMaterial } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create materials for the board.
 */
export function createBoardMaterials(textures: Texture.Collection): Material.Collection {
  return {
    board: createMaterial({
      texture: textures.board,
      type: Material.Type.Lambert,
    }),

    boardNotation: createMaterial({
      options: { transparent: true, depthWrite: false },
      texture: textures.boardNotation,
      textureFlip: true,
      type: Material.Type.Lambert,
    }),

    squareOver: createMaterial({
      options: {
        color: new THREE.Color(0xffffff),
      },
      type: Material.Type.Lambert,
    }),

    squareSelected: createMaterial({
      options: {
        color: new THREE.Color(0x176a85),
        opacity: 0.6,
        transparent: true,
      },
      type: Material.Type.Lambert,
    }),
  };
}
