import * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Destroy the WebGL 3D board renderer.
 */
export function destroy(state: WebGLRenderer3D.State): void {
  const { containerEl, renderer, scene } = state;

  removeSceneObjects(scene);

  disposeCachedObjects(state);
  disposeCachedMaterials(state);

  renderer.dispose();

  const promotionWindow = containerEl.querySelector(`.${Constants.CssClasses.PromotionWindow}`);
  const statisticsPanel = containerEl.querySelector(`.${Constants.CssClasses.StatisticsPanel}`);

  promotionWindow?.parentNode?.removeChild(promotionWindow);
  statisticsPanel?.parentNode?.removeChild(statisticsPanel);
}

/**
 * Remove the objects from the scene.
 */
function removeSceneObjects(obj: THREE.Object3D): void {
  while (obj.children.length > 0) {
    removeSceneObjects(obj.children[0]);

    obj.remove(obj.children[0]);
  }

  if (!(obj instanceof THREE.Mesh)) {
    return;
  }

  if (obj.geometry) {
    obj.geometry.dispose();
  }

  if (obj.material) {
    disposeMaterial(obj.material);
  }
}

/**
 * Remove the cached objects.
 */
function disposeCachedObjects(state: WebGLRenderer3D.State): void {
  const {
    boardModel,
    boardNotationPlane,
    groundModel,
    hoverSquare,
    piecesGeom,
    highlightGeom,
    moveHintGeom,
    captureHintGeom,
    customItemGeom,
  } = state;

  if (boardModel) {
    boardModel.geometry.dispose();

    disposeMaterial(boardModel.material);
  }

  if (boardNotationPlane) {
    boardNotationPlane.geometry.dispose();

    disposeMaterial(boardNotationPlane.material);
  }

  if (groundModel) {
    groundModel.geometry.dispose();

    disposeMaterial(groundModel.material);
  }

  if (hoverSquare) {
    hoverSquare.geometry.dispose();

    disposeMaterial(hoverSquare.material);
  }

  Object.keys(piecesGeom).forEach((key) => piecesGeom[key].dispose());

  highlightGeom.dispose();
  moveHintGeom.dispose();
  captureHintGeom.dispose();
  customItemGeom.dispose();
}

/**
 * Remove the cached materials.
 */
function disposeCachedMaterials(state: WebGLRenderer3D.State): void {
  const { materials, shadowMaterials } = state;

  Object.keys(materials).forEach((key) => disposeMaterial(materials[key]));
  Object.keys(shadowMaterials).forEach((key) => disposeMaterial(shadowMaterials[key]));
}

/**
 * Dispose material resources.
 */
function disposeMaterial(material: THREE.Material | THREE.Material[]): void {
  if (material instanceof Array) {
    material.forEach((mat) => disposeMaterial(mat));

    return;
  }

  // Dispose textures: map, bumpMap, normalMap, etc.
  Object.keys(material).forEach((prop) => {
    if (!material[prop]) {
      return;
    }

    if (typeof material[prop].dispose === 'function') {
      material[prop].dispose();
    }
  });

  material.dispose();
}
