import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { saveCameraSettings } from 'chessboard/renderers/webgl/three-d/camera';

/**
 * On pointer up.
 */
export function onPointerUp(state: WebGLRenderer3D.State): void {
  const { cameraState, containerEl, isPromotionWindowVisible, options } = state;

  if (!options.enabled || isPromotionWindowVisible) {
    return;
  }

  if (cameraState.isRotating || cameraState.isPanning) {
    cameraState.isRotating = false;
    cameraState.isPanning = false;

    saveCameraSettings({ isFlipped: options.flipped, state: cameraState });
  }

  containerEl.classList.remove(Constants.CssClasses.PieceDrag);
}
