import * as THREE from 'three';

/**
 * Create a material.
 */
export function createMaterial({
  type,
  options,
  texture,
  textureFlip,
}: {
  type: string;
  options?: object;
  texture?: THREE.Texture;
  textureFlip?: boolean;
}): THREE.Material {
  const ThreeMaterial: any = <unknown>THREE[<keyof typeof THREE>type];
  const material = new ThreeMaterial();

  if (options) {
    Object.assign(material, options);
  }

  if (texture) {
    material.map = texture;
    material.map.flipY = textureFlip || false;
  }

  return material;
}
