import * as THREE from 'three';
import * as icons from 'chessboard/plugins/custom-items/renderer/icons';
import { Constants as BaseConstants } from 'chessboard/renderers/webgl/base/constants';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import { Game } from 'chessboard/model/game/types';
import {
  loadTexture,
  rotateAroundAxis,
  textureFromSVG,
} from 'chessboard/renderers/webgl/three-d/utils';
import { loadAnimatedTexture } from 'chessboard/renderers/webgl/three-d/utils/load-animated-texture';
import { setSVGAttributes } from 'chessboard/renderers/webgl/base/utils';
import { VisualOrder } from 'chessboard/plugins/custom-items/config/types';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { listenForImageFailure } from '../utils/listen-for-image-failure';

/**
 * Create the visual object for a custom item.
 */
export function createCustomItemObj(
  state: WebGLRenderer3D.State,
  itemInfo: WebGLRenderer.CustomItem.Info,
): THREE.Mesh {
  const { customItemGeom } = state;
  const { position, type, image, frame, opacity, visualOrder } = itemInfo;

  const material = new THREE.MeshBasicMaterial({
    opacity: image && opacity ? opacity : BaseConstants.CustomItem.Opacity,
    depthWrite: false,
    depthTest: type !== Game.Node.Marking.CustomItem.Types.TextBox,
    transparent: true,
  });

  let squareSizeRatio = 1;

  if (image) {
    const texture = new THREE.TextureLoader().load(image.url, (loadedTexture) => {
      listenForImageFailure(loadedTexture.image);
    });

    material.map = texture;

    if (image.percentOfSquare) {
      squareSizeRatio = image.percentOfSquare * 0.01;
    }
  } else if (frame) {
    if (frame.imageAnimated) {
      loadAnimatedTexture(frame.imageURL, { transparent: true }).then((texture) => {
        listenForImageFailure(texture.image);

        material.map = texture;
        material.needsUpdate = true;
      });
    } else {
      loadTexture(frame.imageURL, {
        frame: {
          xRatio: frame.positionRatioX,
          yRatio: frame.positionRatioY,
          widthRatio: frame.widthRatio,
          heightRatio: frame.heightRatio,
          cropRatio: BaseConstants.CustomItem.FrameCropRatio,
        },
        transparent: true,
      }).then((texture) => {
        listenForImageFailure(texture.image);

        material.map = texture;
        material.needsUpdate = true;
      });
    }

    material.opacity = 1;
  } else {
    const svgMarkup = setSVGAttributes(icons[<keyof typeof icons>type], itemInfo);

    material.map = textureFromSVG(svgMarkup);

    if (state.options.test) {
      const svgTestEl = document.createElement('div');
      svgTestEl.innerHTML = svgMarkup;

      (<any>itemInfo).testSVG = svgTestEl.querySelector('svg');
    }
  }

  const mesh = new THREE.Mesh(customItemGeom, material);

  mesh.scale.x *= squareSizeRatio;
  mesh.scale.y *= squareSizeRatio;

  mesh.position.set(position.x, Constants.CustomItem.PositionY, position.y);
  mesh.renderOrder = Constants.ObjectsRenderOrder.CustomItem;

  if (visualOrder) {
    if (visualOrder.target === VisualOrder.Targets.Arrows) {
      mesh.renderOrder = Constants.ObjectsRenderOrder.Arrow;
    } else if (visualOrder.target === VisualOrder.Targets.Effects) {
      mesh.renderOrder = Constants.ObjectsRenderOrder.Effect;
    }
    // Ignore pieces, the custom items should always show on top of them to be easier to see.

    mesh.renderOrder =
      visualOrder.position === VisualOrder.Positions.Above
        ? (mesh.renderOrder += 1)
        : (mesh.renderOrder -= 1);
  }

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: mesh,
  });

  return mesh;
}
