import { Game } from 'model/game/types';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import {
  getPromotionWindowCloseButtonId,
  getPromotionWindowPiecesContainerId,
} from 'chessboard/renderers/webgl/three-d/promotion-window/get-promotion-window-identifiers';
import { getColorAsWord } from 'model/game/get-color-as-word';
import { getWebglAssetUrl } from 'src/utils/get-webgl-asset-url';
import { setTestData, TestElements, TestKeys } from 'src/utils/set-test-data';
import type { ThemeAssets } from 'chessboard/config/theme-assets';

/**
 * Create the promotion window.
 */
export function createPromotionWindow(state: WebGLRenderer3D.State): void {
  state.promotionWindowEl = document.createElement('div');
  state.promotionWindowEl.className = Constants.CssClasses.PromotionWindow;
  state.promotionWindowEl.innerHTML = getPromotionWindowHtml(state);

  if (state.options.test) {
    setTestData(state.promotionWindowEl, {
      [TestKeys.Element]: TestElements.PromotionWindow,
    });

    setTestData(state.promotionWindowEl.getElementsByClassName('close-button')[0] as HTMLElement, {
      [TestKeys.Element]: TestElements.PromotionCloseButton,
    });

    for (const pieceEl of state.promotionWindowEl.getElementsByClassName('promotion-piece')) {
      setTestData(pieceEl as HTMLElement, {
        [TestKeys.Element]: TestElements.PromotionPiece,
        [TestKeys.Type]: pieceEl.getAttribute('data-pieceType') as string,
      });
    }
  }

  state.containerEl.appendChild(state.promotionWindowEl);
}

/**
 * Get the HTML for the promotion window.
 */
function getPromotionWindowHtml(state: WebGLRenderer3D.State): string {
  return `
    ${getPromotionPiecesContainerHtml(state, Game.ColorsAsNumbers.White)}
    ${getPromotionPiecesContainerHtml(state, Game.ColorsAsNumbers.Black)}
    <i
      id="${getPromotionWindowCloseButtonId(state.boardId)}"
      class="close-button icon-font-chess x"></i>
  `;
}

/**
 * Get the HTML for the pieces container in the promotion window.
 */
function getPromotionPiecesContainerHtml(
  state: WebGLRenderer3D.State,
  color: Game.ColorsAsNumbers,
): string {
  const pieces = [
    { name: 'queen', type: 'q' },
    { name: 'knight', type: 'n' },
    { name: 'rook', type: 'r' },
    { name: 'bishop', type: 'b' },
  ];

  const piecesContainerId = `${getPromotionWindowPiecesContainerId(state.boardId, color)}`;

  let html = `<div id="${piecesContainerId}" style="display: none;">`;

  pieces.forEach((piece) => {
    html += getPromotionPieceHtml({
      color,
      piece,
      pieceStyle: state.pieceStyle,
      themeAssets: state.options.themeAssets,
    });
  });

  html += '</div>';

  return html;
}

/**
 * Get the HTML for a piece in the promotion window.
 */
function getPromotionPieceHtml({
  color,
  piece,
  pieceStyle,
  themeAssets,
}: {
  color: Game.ColorsAsNumbers;
  piece: { name: string; type: string };
  pieceStyle: string;
  themeAssets?: ThemeAssets.Assets3D;
}): string {
  const colorName = getColorAsWord(color);

  const pieceUrl =
    themeAssets?.pieces.assets[`${colorName[0]}${piece.type}Promotion`] ||
    getWebglAssetUrl(`three-d/pieces/${pieceStyle}/${piece.name}_promotion_${colorName}.jpg`);

  return `<img class="promotion-piece" data-pieceType="${piece.type}" src="${pieceUrl}" />`;
}
