import { Game } from 'chessboard/model/game/types';
import { ChessError } from 'chessboard/model/error';
import { ErrorCodes } from 'chessboard/model/error/types';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { initPromotionWindow } from 'chessboard/renderers/webgl/three-d/promotion-window/init-promotion-window';
import { getPromotionWindowPiecesContainerId } from 'chessboard/renderers/webgl/three-d/promotion-window/get-promotion-window-identifiers';

/**
 * Open the promotion window.
 */
export function openPromotionWindow(
  state: WebGLRenderer3D.State,
  promotionMove: Game.Move.Model,
): Promise<Game.Piece.Types | undefined> {
  if (!state.promotionWindowEl) {
    throw new ChessError({
      code: ErrorCodes.ElementNotFound,
      message: 'Promotion window does not exist.',
    });
  }

  if (!state.isPromotionWindowInitialized) {
    initPromotionWindow(state);
  }

  const whitePiecesContainer = document.getElementById(
    `${getPromotionWindowPiecesContainerId(state.boardId, Game.ColorsAsNumbers.White)}`,
  );
  const blackPiecesContainer = document.getElementById(
    `${getPromotionWindowPiecesContainerId(state.boardId, Game.ColorsAsNumbers.Black)}`,
  );

  if (!whitePiecesContainer) {
    throw new ChessError({
      code: ErrorCodes.ElementNotFound,
      message: 'White pieces container for promotion window does not exist.',
    });
  }

  if (!blackPiecesContainer) {
    throw new ChessError({
      code: ErrorCodes.ElementNotFound,
      message: 'Black pieces container for promotion window does not exist.',
    });
  }

  if (promotionMove.color === Game.ColorsAsNumbers.White) {
    blackPiecesContainer.style.display = 'none';
    whitePiecesContainer.style.display = 'block';
  } else {
    blackPiecesContainer.style.display = 'block';
    whitePiecesContainer.style.display = 'none';
  }

  state.promotionWindowEl.style.display = 'block';

  return new Promise((resolve) => (state.promotionHandler = resolve));
}
