import type * as THREE from 'three';
import type { Animator } from 'chessboard/renderers/model/animator/types';

/**
 * Animate a texture with all its frames in a single row.
 */
export function animateTexture(
  texture: THREE.Texture,
  {
    animator,
    frameRate = 24,
    onEnd,
  }: {
    animator: Animator.API;
    frameRate?: number;
    onEnd?: () => void;
  },
): void {
  const image = texture.image;
  const framesCount = Math.ceil(image.width / image.height);
  const frameSize = 1 / framesCount;
  const frameDuration = 1 / framesCount;

  const animation = animator.play({
    duration: (framesCount / frameRate) * 1000,
    data: { currentFrame: 1 },
    onUpdate: () => {
      const { data, frameInfo } = animation;
      const readyForNextFrame = frameDuration * data.currentFrame < frameInfo.elapsedInterval;

      if (readyForNextFrame) {
        texture.offset.x = frameSize * data.currentFrame;

        data.currentFrame++;
      }
    },
    onEnd,
  });
}
