import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { findPieceFromFileRank } from 'chessboard/renderers/webgl/base/pieces/find-piece-from-file-rank';
import { positionToFileRank } from 'chessboard/renderers/webgl/three-d/main';

/**
 * Find piece from board position.
 */
export function findPieceFromPosition(
  state: WebGLRenderer3D.State,
  position: WebGLRenderer.BoardPosition,
): WebGLRenderer3D.Piece.Model | null {
  const fileRank = positionToFileRank(state, position);

  return findPieceFromFileRank(state, fileRank);
}
