import type { Animator } from 'chessboard/renderers/model/animator/types';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Fade-in all pieces.
 */
export function fadeInAllPieces(
  state: WebGLRenderer3D.State,
  duration: number,
): Animator.Animation.Model {
  const { animator, materials, shadowMaterials } = state;

  const whitePieceTransparency = materials.whitePiece.transparent;
  const blackPieceTransparentcy = materials.blackPiece.transparent;

  materials.whitePiece.transparent = true;
  materials.blackPiece.transparent = true;

  return animator.play({
    duration,
    keyframes: {
      0: {
        whitePieceOpacity: 0,
        blackPieceOpacity: 0,

        bishopShadowOpacity: 0,
        kingShadowOpacity: 0,
        knightShadowOpacity: 0,
        pawnShadowOpacity: 0,
        queenShadowOpacity: 0,
        rookShadowOpacity: 0,
      },
      100: {
        whitePieceOpacity: materials.whitePiece.opacity,
        blackPieceOpacity: materials.blackPiece.opacity,

        bishopShadowOpacity: shadowMaterials.bishop.opacity,
        kingShadowOpacity: shadowMaterials.king.opacity,
        knightShadowOpacity: shadowMaterials.knight.opacity,
        pawnShadowOpacity: shadowMaterials.pawn.opacity,
        queenShadowOpacity: shadowMaterials.queen.opacity,
        rookShadowOpacity: shadowMaterials.rook.opacity,
      },
    },
    onUpdate: ({ animatedProps }) => {
      materials.whitePiece.opacity = animatedProps.whitePieceOpacity;
      materials.blackPiece.opacity = animatedProps.blackPieceOpacity;

      shadowMaterials.bishop.opacity = animatedProps.bishopShadowOpacity;
      shadowMaterials.king.opacity = animatedProps.kingShadowOpacity;
      shadowMaterials.knight.opacity = animatedProps.knightShadowOpacity;
      shadowMaterials.pawn.opacity = animatedProps.pawnShadowOpacity;
      shadowMaterials.queen.opacity = animatedProps.queenShadowOpacity;
      shadowMaterials.rook.opacity = animatedProps.rookShadowOpacity;
    },
    onEnd: () => {
      materials.whitePiece.transparent = whitePieceTransparency;
      materials.blackPiece.transparent = blackPieceTransparentcy;
    },
  });
}
