import * as THREE from 'three';
import type { AnimationHandlers, CameraState } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Rotates the camera.
 */
export function rotateCamera({
  state,
  panAngle,
  tiltAngle,
  animation,
}: {
  state: CameraState;
  panAngle: number;
  tiltAngle: number;
  animation?: AnimationHandlers;
}): Promise<void> {
  if (animation) {
    return animation.rotateCamera({ panAngle, state, tiltAngle });
  }

  return new Promise((resolve) => {
    const tiltAngleSanitized = Math.min(180, Math.max(0, tiltAngle));
    const newCameraPosition = calculatePositionForCameraRotation({
      distance: state.distance,
      panAngle,
      target: state.target,
      tiltAngle: tiltAngleSanitized,
    });

    state.camera.position.copy(newCameraPosition);
    state.camera.lookAt(state.target);

    state.panAngle = panAngle;
    state.tiltAngle = tiltAngleSanitized;

    resolve();
  });
}

/**
 * Calculate the position needed for camera rotation.
 */
function calculatePositionForCameraRotation({
  distance,
  target,
  panAngle,
  tiltAngle,
}: {
  distance: number;
  target: THREE.Vector3;
  panAngle: number;
  tiltAngle: number;
}): THREE.Vector3 {
  const x =
    distance * Math.sin((panAngle * Math.PI) / 360) * Math.cos((tiltAngle * Math.PI) / 360) +
    target.x;

  const y = distance * Math.sin((tiltAngle * Math.PI) / 360);

  const z =
    distance * Math.cos((panAngle * Math.PI) / 360) * Math.cos((tiltAngle * Math.PI) / 360) +
    target.z;

  return new THREE.Vector3(x, y, z);
}
