import * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { rotateAroundAxis } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create the plane object that will be displayed when the mouse gets over a board square.
 */
export function createHoverSquareObj(state: WebGLRenderer3D.State): THREE.Mesh {
  const shape = new THREE.Shape();
  const hole = new THREE.Path();

  const shapePoint = Constants.Square.HalfSize;
  const holePoint = Constants.Square.HalfSize - Constants.Square.OverBorderSize;

  shape.moveTo(-shapePoint, -shapePoint);
  shape.lineTo(shapePoint, -shapePoint);
  shape.lineTo(shapePoint, shapePoint);
  shape.lineTo(-shapePoint, shapePoint);
  shape.lineTo(-shapePoint, -shapePoint);

  hole.moveTo(-holePoint, -holePoint);
  hole.lineTo(holePoint, -holePoint);
  hole.lineTo(holePoint, holePoint);
  hole.lineTo(-holePoint, holePoint);
  hole.lineTo(-holePoint, -holePoint);

  shape.holes.push(hole);

  const hoverSquare = new THREE.Mesh(new THREE.ShapeGeometry(shape));

  hoverSquare.position.set(
    Constants.Square.Size,
    Constants.Square.InvisibleY,
    Constants.Square.Size,
  );

  hoverSquare.renderOrder = Constants.ObjectsRenderOrder.OverSquare;

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: hoverSquare,
  });

  state.scene.add(hoverSquare);

  return hoverSquare;
}
