import type { Board as BoardTypes } from 'chessboard/model/board/types';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Get the piece from a SAN square.
 */
export function getPieceFromSanSquare(
  square: BoardTypes.SanSquare,
  pieces: WebGLRenderer3D.Piece.Collection,
): WebGLRenderer3D.Piece.Model | null {
  const foundPiece = Object.values(pieces).find((piece) => {
    if (piece.info.square === square) {
      return true;
    }

    return false;
  });

  return foundPiece || null;
}
