import * as THREE from 'three';
import type { Board } from 'chessboard/model/board/types';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Get the 2D board position from normalized mouse point coordinates.
 */
export function getBoardPosition(
  state: WebGLRenderer3D.State,
  coords: Board.Coords,
): WebGLRenderer.BoardPosition {
  const { cameraState, viewEl } = state;
  const { camera } = cameraState;

  const projectedPosition = new THREE.Vector3(
    (coords.x / viewEl.width) * 2 - 1,
    -(coords.y / viewEl.height) * 2 + 1,
    1,
  );

  projectedPosition.unproject(camera);

  const cameraPosition = camera.position;
  const m = projectedPosition.y / (projectedPosition.y - cameraPosition.y);

  return {
    x: projectedPosition.x + (cameraPosition.x - projectedPosition.x) * m,
    y: projectedPosition.z + (cameraPosition.z - projectedPosition.z) * m,
  };
}
