import type { CameraState } from 'chessboard/renderers/webgl/three-d/types';
import { Constants } from 'chessboard/renderers/webgl/three-d/animations/default/config/constants';
import { easeInOutQuadratic } from 'chessboard/renderers/model/animator/easing';

/**
 * Animates camera distance.
 */
export function changeCameraDistance(state: CameraState, distance: number): Promise<void> {
  return new Promise((resolve) => {
    state.animator.play({
      duration: Constants.Camera.Distance.Duration,
      keyframes: {
        0: {
          distance: state.distance,
        },
        100: {
          distance,
        },
      },
      easing: easeInOutQuadratic,
      onUpdate: (animation) => {
        state.distance = animation.animatedProps.distance;
      },
      onEnd: () => resolve(),
    });
  });
}
