import * as THREE from 'three';
import type { Texture } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Create a texture from an image URL.
 */
export function loadTexture(
  imageURL: string,
  {
    frame,
    transparent = false,
  }: {
    frame?: Texture.Frame;
    transparent?: boolean;
  } = {},
): Promise<THREE.Texture> {
  return new Promise((resolve) => {
    new THREE.TextureLoader().load(imageURL, (texture) => {
      if (frame) {
        const { xRatio, yRatio, widthRatio, heightRatio, cropRatio } = frame;

        texture.offset.x = xRatio;
        texture.offset.y = 1 - yRatio - heightRatio; // coordinates starts from bottom left
        texture.repeat.x = widthRatio;
        texture.repeat.y = heightRatio;

        if (cropRatio) {
          texture.offset.x += (widthRatio - widthRatio * cropRatio) / 2;
          texture.offset.y += (heightRatio - heightRatio * cropRatio) / 2;
          texture.repeat.x *= cropRatio;
          texture.repeat.y *= cropRatio;
        }
      }

      if (transparent) {
        // fix dark border around transparent textures
        texture.minFilter = THREE.NearestFilter;
      }

      resolve(texture);
    });
  });
}
