import type { CameraState } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Pan the camera vertically.
 */
export function panCameraVertically(state: CameraState, panDistance: number): void {
  state.target.y += panDistance;
  state.camera.lookAt(state.target);

  state.verticalPanning = panDistance;
}
