import * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { rotateAroundAxis } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create the visual object for an highlighted square.
 */
export function createHighlightObj(
  state: WebGLRenderer3D.State,
  highlightInfo: WebGLRenderer.Highlight.Info,
): THREE.Mesh {
  const { highlightGeom } = state;
  const { color, opacity, position } = highlightInfo;

  const material = new THREE.MeshBasicMaterial({
    color,
    opacity,
  });

  material.transparent = true;
  material.depthWrite = false;

  const mesh = new THREE.Mesh(highlightGeom, material);

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: mesh,
  });

  mesh.renderOrder = Constants.ObjectsRenderOrder.HighligtedSquare;

  mesh.position.set(position.x, 0.005, position.y);

  return mesh;
}
