import type * as THREE from 'three';
import type { Animator } from 'chessboard/renderers/model/animator/types';
import type { Game } from 'chessboard/model/game/types';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { Board } from 'chessboard/model/board/types';
import type { ThemeAssets } from 'chessboard/config/theme-assets';

export namespace WebGLRenderer3D {
  export interface State extends WebGLRenderer.State {
    renderer: THREE.WebGLRenderer;
    scene: THREE.Scene;
    cameraState: CameraState;

    options: Board.Options<ThemeAssets.Assets3D>;

    lights: Light.Collection;
    materials: Material.Collection;
    shadowMaterials: Material.Collection;

    analysisOverlay: THREE.Mesh | null;
    boardModel: THREE.Mesh | null;
    boardNotationPlane: THREE.Mesh | null;
    groundModel: THREE.Mesh | null;
    hoverSquare: THREE.Mesh | null;
    promotionWindowEl?: HTMLElement;

    highlightGeom: THREE.PlaneBufferGeometry;
    moveHintGeom: THREE.CircleGeometry;
    captureHintGeom: THREE.RingGeometry;
    piecesGeom: Geometry.Collection;
    customItemGeom: THREE.PlaneBufferGeometry;

    isBoardFlipping: boolean;
    isPromotionWindowInitialized: boolean;

    overlappedPiece: Piece.Model | null;

    arrows: Arrow.Collection;
    blinkingHighlights: Highlight.Collection;
    customItems: CustomItem.Collection;
    effects: Effect.Collection;
    highlights: Highlight.Collection;
    hints: Hint.Collection;

    // TODO: RETHINK THIS!
    animation: AnimationHandlers;

    promotionHandler: (pieceType?: Game.Piece.Types) => void;
  }

  export namespace Piece {
    export type Model = WebGLRenderer.Piece.Model<THREE.Object3D>;
    export type Collection = WebGLRenderer.Piece.Collection<THREE.Object3D>;
  }

  export namespace Arrow {
    export type Model = WebGLRenderer.Arrow.Model<THREE.Mesh>;
    export type Collection = WebGLRenderer.Arrow.Collection<THREE.Mesh>;
  }

  export namespace CustomItem {
    export type Model = WebGLRenderer.CustomItem.Model<THREE.Mesh>;
    export type Collection = WebGLRenderer.CustomItem.Collection<THREE.Mesh>;
  }

  export namespace Effect {
    export type Model = WebGLRenderer.Effect.Model<THREE.Mesh[]>;
    export type Collection = WebGLRenderer.Effect.Collection<THREE.Mesh[]>;
  }

  export namespace Highlight {
    export type Model = WebGLRenderer.Highlight.Model<THREE.Mesh>;
    export type Collection = WebGLRenderer.Highlight.Collection<THREE.Mesh>;
  }

  export namespace Hint {
    export type Model = WebGLRenderer.Hint.Model<THREE.Mesh>;
    export type Collection = WebGLRenderer.Hint.Collection<THREE.Mesh>;
  }
}

export interface CameraState {
  camera: THREE.PerspectiveCamera;
  distance: number;
  panAngle: number;
  panAngleFlipped: number;
  tiltAngle: number;
  horizontalPanning: number;
  verticalPanning: number;
  isRotating: boolean;
  isPanning: boolean;
  target: THREE.Vector3;
  animations: {
    panning: any | null;
    rotation: any | null;
  };
  animator: Animator.API;
}

export interface CameraSettings {
  target: Coords3D;
  distance: number;
  panAngle: number;
  tiltAngle: number;
  horizontalPanning: number;
  verticalPanning: number;
}

export enum ObjectType {
  Board,
  Piece,
}

export interface Coords3D {
  x: number;
  y: number;
  z: number;
}

export interface AnimationHandlers {
  changeCameraDistance(state: CameraState, distance: number): Promise<void>;
  rotateCamera(options: { state: CameraState; panAngle: number; tiltAngle: number }): Promise<void>;
}

export namespace Geometry {
  export interface Collection {
    [key: string]: THREE.BufferGeometry;
  }
}

export namespace Light {
  export interface Collection {
    [key: string]: THREE.Light;
  }
}

export namespace Texture {
  export interface Collection {
    [key: string]: THREE.Texture;
  }

  export interface Frame {
    xRatio: number;
    yRatio: number;
    widthRatio: number;
    heightRatio: number;
    cropRatio?: number;
  }
}

export namespace Material {
  export interface Collection {
    [key: string]: THREE.Material;
  }

  export enum Type {
    Basic = 'MeshBasicMaterial',
    Lambert = 'MeshLambertMaterial',
    Phong = 'MeshPhongMaterial',
  }
}
