import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { getPieceFromMousePoint } from 'chessboard/renderers/webgl/three-d/utils/get-piece-from-mouse-point';
import { fadePiece } from 'chessboard/renderers/webgl/three-d/pieces/fade-piece';

/**
 * On piece drag.
 */
export function onPieceDrag(
  state: WebGLRenderer3D.State,
  piece: WebGLRenderer3D.Piece.Model,
): void {
  // Lift piece
  if (piece.obj.children.length) {
    piece.obj.children[0].position.y = 1;
  }

  fadeOverlappedPiece(state, piece);
}

/**
 * Make the overlapped piece semi-transparent.
 */
function fadeOverlappedPiece(
  state: WebGLRenderer3D.State,
  draggedPiece: WebGLRenderer3D.Piece.Model,
): void {
  if (state.overlappedPiece) {
    fadePiece({
      materials: state.materials,
      piece: state.overlappedPiece,
      shouldFade: false,
    });
  }

  state.overlappedPiece = getPieceFromMousePoint(draggedPiece.obj.position, state.pieces);

  if (
    state.overlappedPiece &&
    state.selectedPiece &&
    state.overlappedPiece.obj !== state.selectedPiece.obj
  ) {
    fadePiece({
      materials: state.materials,
      piece: state.overlappedPiece,
      shouldFade: true,
    });
  }
}
