import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Show the hover square.
 */
export function showHoverSquare(
  state: WebGLRenderer3D.State,
  position: WebGLRenderer.BoardPosition,
): void {
  const { hoverSquare } = state;

  if (!hoverSquare) {
    return;
  }

  hoverSquare.position.x = position.x;
  hoverSquare.position.y = Constants.Square.VisibleY;
  hoverSquare.position.z = position.y;
}
