import type { CameraState } from 'chessboard/renderers/webgl/three-d/types';
import { panCameraHorizontally } from 'chessboard/renderers/webgl/three-d/camera/pan-camera-horizontally';
import { panCameraVertically } from 'chessboard/renderers/webgl/three-d/camera/pan-camera-vertically';

/**
 * Pan the camera.
 */
export function panCamera({
  state,
  panH,
  panV,
}: {
  state: CameraState;
  panH: number;
  panV: number;
}): void {
  panCameraHorizontally(state, panH);
  panCameraVertically(state, panV);
}
