import type { Texture } from 'chessboard/renderers/webgl/three-d/types';
import { Material } from 'chessboard/renderers/webgl/three-d/types';
import { createMaterial } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create materials for the pieces.
 */
export function createPieceMaterials(textures: Texture.Collection): Material.Collection {
  return {
    blackPiece: createMaterial({
      options: { shininess: 40 },
      texture: textures.pieceBlack,
      type: Material.Type.Phong,
    }),
    blackPieceCaptured: createMaterial({
      options: { transparent: true },
      texture: textures.pieceBlack,
      type: Material.Type.Phong,
    }),
    blackPieceOverlapped: createMaterial({
      options: { opacity: 0.5, transparent: true },
      texture: textures.pieceBlack,
      type: Material.Type.Phong,
    }),

    whitePiece: createMaterial({
      options: { shininess: 30 },
      texture: textures.pieceWhite,
      type: Material.Type.Phong,
    }),
    whitePieceCaptured: createMaterial({
      options: { transparent: true },
      texture: textures.pieceWhite,
      type: Material.Type.Phong,
    }),
    whitePieceOverlapped: createMaterial({
      options: { opacity: 0.5, transparent: true },
      texture: textures.pieceWhite,
      type: Material.Type.Phong,
    }),
  };
}
