import * as THREE from 'three';
import type { Texture, WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { getTexturesToLoad } from 'chessboard/renderers/webgl/three-d/main/get-textures-to-load';
import { defaultLegacyBoardStyles } from 'chessboard/config/legacy-themes';

/**
 * Load all textures.
 */
export function loadTextures(state: WebGLRenderer3D.State): Promise<Texture.Collection> {
  const {
    boardThemes,
    boardStyle,
    options: { themeAssets },
  } = state;

  const textureCollection: Texture.Collection = {};

  const theme = boardThemes[boardStyle] ?? defaultLegacyBoardStyles.green;

  const texturesToLoad = getTexturesToLoad(theme[2], state.pieceStyle, themeAssets);
  const textureLoader = new THREE.TextureLoader();
  const loadedTexturesPromises: Promise<THREE.Texture>[] = [];

  texturesToLoad.forEach((item) => {
    loadedTexturesPromises.push(
      new Promise((resolve) => {
        textureCollection[item.name] = textureLoader.load(item.url, resolve);
      }),
    );
  });

  return new Promise((resolve) =>
    Promise.all(loadedTexturesPromises).then(() => {
      resolve(textureCollection);
    }),
  );
}
