import type { Animator } from 'chessboard/renderers/model/animator/types';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { easeInOutQuartic } from 'chessboard/renderers/model/animator/easing';

/**
 * Animate a blinking highlight.
 */
export function blinkHighlight(
  state: WebGLRenderer3D.State,
  {
    highlight,
    interval,
    times,
  }: {
    highlight: WebGLRenderer3D.Highlight.Model;
    interval: number;
    times: number;
  },
): Animator.Animation.Model {
  return state.animator.play({
    duration: interval,
    easing: easeInOutQuartic,
    keyframes: {
      0: {
        opacity: 0,
      },
      50: {
        opacity: 1,
      },
      100: {
        opacity: 0,
      },
    },
    repeat: times,
    target: highlight.obj.material,
  });
}
