import * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { arrowStyles, flippedKnightArrowSlopes } from 'chessboard/config/arrows';
import { calculateArrowLength } from 'chessboard/model/arrows/calculate-arrow-length';
import { calculateArrowRotation } from 'chessboard/model/arrows/calculate-arrow-rotation';
import { calculateArrowSlope } from 'chessboard/model/arrows/calculate-arrow-slope';
import { fileRankToPosition } from 'chessboard/renderers/webgl/three-d/main';
import { getKnightArrowRotation } from 'chessboard/model/arrows/get-knight-arrow-rotation';
import { rotateAroundAxis } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create the visual object for an arrow.
 */
export function createArrowObj(
  state: WebGLRenderer3D.State,
  arrowInfo: WebGLRenderer.Arrow.Info,
): THREE.Mesh {
  const { color, opacity, isKnight } = arrowInfo;

  const from = fileRankToPosition(state, arrowInfo.from);
  const to = fileRankToPosition(state, arrowInfo.to);

  const length = calculateArrowLength({ from, to });
  const slope = calculateArrowSlope({ from, to });
  const flip = flippedKnightArrowSlopes.includes(slope) ? -1 : 1;

  const rotation = isKnight
    ? getKnightArrowRotation({ from, slope, to })
    : calculateArrowRotation({ from, to });

  const shape = getShape(length, isKnight);
  const geometry = new THREE.ShapeGeometry(shape);

  const mesh = new THREE.Mesh(
    geometry,
    new THREE.MeshLambertMaterial({
      color: new THREE.Color(color),
      depthTest: false,
      depthWrite: false,
      opacity,
      transparent: true,
    }),
  );

  mesh.position.x = from.x;
  mesh.position.y = Constants.Arrow.PositionY;
  mesh.position.z = from.y;

  mesh.scale.x *= flip;
  mesh.renderOrder = Constants.ObjectsRenderOrder.Arrow;

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: mesh,
  });

  rotateAroundAxis({
    axis: new THREE.Vector3(0, 0, 1),
    degrees: -rotation,
    object: mesh,
  });

  return mesh;
}

/**
 * Get the arrow shape.
 */
function getShape(length: number, isKnight: boolean): THREE.Shape {
  const squareSize = Constants.Square.Size;
  const boardWidth = squareSize * 8;

  const bodyWidth = boardWidth * arrowStyles.WIDTH * 0.01;
  const bodyHalfWidth = bodyWidth * 0.5;

  const headHeight = boardWidth * arrowStyles.HEAD_HEIGHT * 0.01;
  const headWidth = boardWidth * arrowStyles.HEAD_WIDTH * 0.01;
  const headHalfWidth = headWidth * 0.5;

  const tailMargin = boardWidth * arrowStyles.TAIL_PADDING * 0.01;

  const shape = new THREE.Shape();

  if (isKnight) {
    shape
      .moveTo(-bodyHalfWidth, -tailMargin)
      .lineTo(bodyHalfWidth, -tailMargin)
      .lineTo(bodyHalfWidth, bodyHalfWidth - squareSize * 2)
      .lineTo(squareSize - headHeight, bodyHalfWidth - squareSize * 2)
      .lineTo(squareSize - headHeight, headHalfWidth - squareSize * 2)
      .lineTo(squareSize, -squareSize * 2)
      .lineTo(squareSize - headHeight, -(squareSize * 2 + headHalfWidth))
      .lineTo(squareSize - headHeight, -(squareSize * 2 + bodyHalfWidth))
      .lineTo(-bodyHalfWidth, -(squareSize * 2 + bodyHalfWidth));
  } else {
    shape
      .moveTo(bodyHalfWidth, -tailMargin)
      .lineTo(-bodyHalfWidth, -tailMargin)
      .lineTo(-bodyHalfWidth, -length + headHeight)
      .lineTo(-headHalfWidth, -length + headHeight)
      .lineTo(0, -length)
      .lineTo(headHalfWidth, -length + headHeight)
      .lineTo(bodyHalfWidth, -length + headHeight);
  }

  return shape;
}
