import type { ThemeAssets } from 'chessboard/config/theme-assets';
import { ObjectType } from 'chessboard/renderers/webgl/three-d/types';
import { getWebglAssetUrl } from 'src/utils/get-webgl-asset-url';

/**
 * Get a list of all the objects to load.
 */
export function getObjectsToLoad(
  pieceStyle: string,
  themeAssets?: ThemeAssets.Assets3D,
): {
  name: string;
  type: ObjectType;
  url: string;
}[] {
  return [
    {
      name: 'board',
      type: ObjectType.Board,
      url: themeAssets?.board.assets.boardModel || getWebglAssetUrl('three-d/board/board.glb'),
    },
    {
      name: 'king',
      type: ObjectType.Piece,
      url:
        themeAssets?.pieces.assets.kModel ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/king.glb`),
    },
    {
      name: 'queen',
      type: ObjectType.Piece,
      url:
        themeAssets?.pieces.assets.qModel ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/queen.glb`),
    },
    {
      name: 'rook',
      type: ObjectType.Piece,
      url:
        themeAssets?.pieces.assets.rModel ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/rook.glb`),
    },
    {
      name: 'bishop',
      type: ObjectType.Piece,
      url:
        themeAssets?.pieces.assets.bModel ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/bishop.glb`),
    },
    {
      name: 'knight',
      type: ObjectType.Piece,
      url:
        themeAssets?.pieces.assets.nModel ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/knight.glb`),
    },
    {
      name: 'pawn',
      type: ObjectType.Piece,
      url:
        themeAssets?.pieces.assets.pModel ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/pawn.glb`),
    },
  ];
}
