import type { CameraState } from 'chessboard/renderers/webgl/three-d/types';
import { Constants } from 'chessboard/renderers/webgl/three-d/animations/default/config/constants';
import { rotateCamera as importedRotateCamera } from 'chessboard/renderers/webgl/three-d/camera';
import { easeInOutQuadratic } from 'chessboard/renderers/model/animator/easing';

/**
 * Animates camera rotation.
 */
export function rotateCamera({
  state,
  panAngle,
  tiltAngle,
}: {
  state: CameraState;
  panAngle: number;
  tiltAngle: number;
}): Promise<void> {
  return new Promise((resolve) => {
    if (state.animations.rotation) {
      state.animations.rotation.stop();
      state.animations.rotation = null;
    }

    state.animations.rotation = state.animator.play({
      duration: Constants.Camera.Rotation.Duration,
      easing: easeInOutQuadratic,
      keyframes: {
        0: {
          pan: state.panAngle,
          tilt: state.tiltAngle,
        },
        100: {
          pan: panAngle,
          tilt: tiltAngle,
        },
      },
      onUpdate: (animation) => {
        const { animatedProps } = animation;

        importedRotateCamera({
          state,
          panAngle: animatedProps.pan,
          tiltAngle: animatedProps.tilt,
        });
      },
      onEnd: () => {
        state.animations.rotation = null;
        resolve();
      },
    });
  });
}
