import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Update the rendered view size.
 */
export function updateViewSize(state: WebGLRenderer3D.State, size: WebGLRenderer.Size): void {
  const { height, width } = size;
  const { cameraState, renderer } = state;

  cameraState.camera.aspect = width / height;
  cameraState.camera.updateProjectionMatrix();

  renderer.setSize(width, height);
}
