import type { Texture } from 'chessboard/renderers/webgl/three-d/types';
import { Material } from 'chessboard/renderers/webgl/three-d/types';
import { createMaterial } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create materials for the shadow planes.
 */
export function createShadowMaterials(textures: Texture.Collection): Material.Collection {
  return {
    bishop: createMaterial({
      options: {
        depthWrite: false,
        transparent: true,
      },
      texture: textures.bishopShadow,
      type: Material.Type.Basic,
    }),
    ground: createMaterial({
      options: { transparent: true },
      texture: textures.boardShadow,
      type: Material.Type.Basic,
    }),
    king: createMaterial({
      options: {
        depthWrite: false,
        transparent: true,
      },
      texture: textures.kingShadow,
      type: Material.Type.Basic,
    }),
    knight: createMaterial({
      options: {
        depthWrite: false,
        transparent: true,
      },
      texture: textures.knightShadow,
      type: Material.Type.Basic,
    }),
    pawn: createMaterial({
      options: {
        depthWrite: false,
        transparent: true,
      },
      texture: textures.pawnShadow,
      type: Material.Type.Basic,
    }),
    queen: createMaterial({
      options: {
        depthWrite: false,
        transparent: true,
      },
      texture: textures.queenShadow,
      type: Material.Type.Basic,
    }),
    rook: createMaterial({
      options: {
        depthWrite: false,
        transparent: true,
      },
      texture: textures.rookShadow,
      type: Material.Type.Basic,
    }),
  };
}
