import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Sets the piece position.
 */
export function setPiecePosition(
  piece: WebGLRenderer3D.Piece.Model,
  position: WebGLRenderer.BoardPosition,
): void {
  piece.obj.position.x = position.x;
  piece.obj.position.z = position.y;
  piece.obj.position.y = 0.01;
}
