import type { ThemeAssets } from 'chessboard/config/theme-assets';
import { getWebglAssetUrl } from 'src/utils/get-webgl-asset-url';

/**
 * Get a list of all the textures to load.
 */
export function getTexturesToLoad(
  boardStyle: string,
  pieceStyle: string,
  themeAssets?: ThemeAssets.Assets3D,
): {
  name: string;
  url: string;
}[] {
  return [
    {
      name: 'board',
      url:
        themeAssets?.board.assets.background ||
        getWebglAssetUrl(`three-d/board/textures/board_${boardStyle}.jpg`),
    },
    {
      name: 'boardNotation',
      url:
        themeAssets?.board.assets.coordinates ||
        getWebglAssetUrl(`three-d/board/textures/board_${boardStyle}_notation.png`),
    },
    {
      name: 'pieceWhite',
      url:
        themeAssets?.pieces.assets.lightTexture ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/textures/piece_white.jpg`),
    },
    {
      name: 'pieceBlack',
      url:
        themeAssets?.pieces.assets.darkTexture ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/textures/piece_black.jpg`),
    },
    {
      name: 'boardShadow',
      url:
        themeAssets?.board.assets.boardShadow ||
        getWebglAssetUrl('three-d/board/textures/board_shadow.png'),
    },
    {
      name: 'kingShadow',
      url:
        themeAssets?.pieces.assets.kShadow ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/textures/king_shadow.png`),
    },
    {
      name: 'queenShadow',
      url:
        themeAssets?.pieces.assets.qShadow ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/textures/queen_shadow.png`),
    },
    {
      name: 'rookShadow',
      url:
        themeAssets?.pieces.assets.rShadow ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/textures/rook_shadow.png`),
    },
    {
      name: 'bishopShadow',
      url:
        themeAssets?.pieces.assets.bShadow ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/textures/bishop_shadow.png`),
    },
    {
      name: 'knightShadow',
      url:
        themeAssets?.pieces.assets.nShadow ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/textures/knight_shadow.png`),
    },
    {
      name: 'pawnShadow',
      url:
        themeAssets?.pieces.assets.pShadow ||
        getWebglAssetUrl(`three-d/pieces/${pieceStyle}/textures/pawn_shadow.png`),
    },
  ];
}
