import * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import { rotateAroundAxis } from 'chessboard/renderers/webgl/three-d/utils';

/**
 * Create the visual object for a piece.
 */
export function createPieceObj(): THREE.Object3D {
  const obj = new THREE.Object3D();

  const bodyMesh = new THREE.Mesh();
  const shadowPlane = createPieceShadow();

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: 90,
    object: bodyMesh,
  });

  obj.add(bodyMesh);
  obj.add(shadowPlane);

  return obj;
}

/**
 * Create the shadow plane for the piece.
 */
function createPieceShadow(): THREE.Mesh {
  const shadowPlane = new THREE.Mesh(
    new THREE.PlaneBufferGeometry(Constants.Square.Size, Constants.Square.Size, 1, 1),
  );

  rotateAroundAxis({
    axis: new THREE.Vector3(1, 0, 0),
    degrees: -90,
    object: shadowPlane,
  });

  shadowPlane.renderOrder = Constants.ObjectsRenderOrder.PieceShadow;

  return shadowPlane;
}
