import 'chessboard/renderers/webgl/three-d/style.scss';
import * as DefaultAnimation from 'chessboard/renderers/webgl/three-d/animations/default';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { CreateRenderer } from 'chessboard/renderers/config/types';
import { Renderer } from 'chessboard/renderers/config/types';
import { createWebGLRendererInstance } from 'chessboard/renderers/webgl/base/main/create-instance';
import {
  destroy,
  fileRankToPosition,
  getBoardPosition,
  getViewPosition,
  getViewAspectRatio,
  init,
  normalizePointer,
  positionToFileRank,
  positionToNearestSquare,
  render,
  updateViewSize,
} from 'chessboard/renderers/webgl/three-d/main';
import { onWebGLContextLost } from 'chessboard/renderers/webgl/three-d/main/on-webgl-context-lost';
import { createState } from 'chessboard/renderers/webgl/three-d/main/create-state';
import { loadAssets } from 'chessboard/renderers/webgl/three-d/assets';
import {
  addAnalysisOverlay,
  flipBoard,
  removeAnalysisOverlay,
  updateBoardCoordinates,
  updateBoardStyle,
} from 'chessboard/renderers/webgl/three-d/board';
import {
  addPiece,
  createPieceObj,
  fadeInAllPieces,
  onPieceDrag,
  onPieceDrop,
  removePiece,
  setPiecePosition,
  updatePiecesStyle,
  updatePieceVisuals,
} from 'chessboard/renderers/webgl/three-d/pieces';
import { addArrow, createArrowObj, removeArrow } from 'chessboard/renderers/webgl/three-d/arrows';
import {
  addHighlight,
  createHighlightObj,
  createHoverSquareObj,
  hideHoverSquare,
  removeHighlight,
  showHoverSquare,
} from 'chessboard/renderers/webgl/three-d/highlights';
import {
  addHint,
  createCaptureHintObj,
  createMoveHintObj,
  removeHint,
} from 'chessboard/renderers/webgl/three-d/hints';
import {
  closePromotionWindow,
  openPromotionWindow,
} from 'chessboard/renderers/webgl/three-d/promotion-window';
import {
  onMouseDoubleClick,
  onMouseWheel,
  onPointerDown,
  onPointerMove,
  onPointerUp,
} from 'chessboard/renderers/webgl/three-d/listeners';
import {
  defaultLegacyBoardStyles,
  defaultLegacyPieceStyles,
} from 'chessboard/config/legacy-themes';
import {
  addCustomItem,
  createCustomItemObj,
  removeCustomItem,
} from 'chessboard/renderers/webgl/three-d/custom-items';
import { addEffect, removeEffect } from 'chessboard/renderers/webgl/three-d/effects';
import { createEffectObj } from 'chessboard/renderers/webgl/three-d/effects/create-effect-obj';
import { EventEmitterUtil } from '@chesscom/web-ui';

const { createEventEmitter } = EventEmitterUtil;

export { createWebGLRenderer3D as createRenderer };

/**
 * Create the renderer for the WebGL 3d board.
 */
export function createWebGLRenderer3D({
  el,
  emitter = createEventEmitter(),
  options,
  boardStyles = defaultLegacyBoardStyles,
  pieceStyles = defaultLegacyPieceStyles,
  testElement,
}: CreateRenderer.Options): Renderer.Model {
  const animationMethods = DefaultAnimation;

  const methods = {
    addAnalysisOverlay,
    addArrow,
    addCustomItem,
    addEffect,
    addHighlight,
    addHint,
    addPiece,
    closePromotionWindow,
    createArrowObj,
    createCaptureHintObj,
    createCustomItemObj,
    createEffectObj,
    // WebGL 3D does not currently support animated effects, so we use the same non animated effect
    createEffectObjAnimated: createEffectObj,
    createHighlightObj,
    createHoverSquareObj,
    createMoveHintObj,
    createPieceObj,
    createState,
    destroy,
    fadeInAllPieces,
    fadeOutEffect: () => new Promise<void>((resolve) => resolve()),
    fileRankToPosition,
    flipBoard,
    getBoardPosition,
    getViewPosition,
    getViewAspectRatio,
    hideHoverSquare,
    init,
    loadAssets,
    normalizePointer,
    onMouseDoubleClick,
    onMouseWheel,
    onPieceDrag,
    onPieceDrop,
    onPointerDown,
    onPointerMove,
    onPointerUp,
    onWebGLContextLost,
    openPromotionWindow,
    positionToFileRank,
    positionToNearestSquare,
    removeAnalysisOverlay,
    removeArrow,
    removeCustomItem,
    removeEffect,
    removeHighlight,
    removeHint,
    removePiece,
    render,
    setPiecePosition,
    showHoverSquare,
    updateBoardCoordinates,
    updateBoardStyle,
    updatePiecesStyle,
    updatePieceVisuals,
    updateViewSize,

    createTakeover: () => {},
    addTakeover: () => {},
    endTakeover: () => {},
    removeTakeover: () => {},
    removeTakeoverByType: () => {},
  };

  const params = {
    animationMethods,
    boardThemes: boardStyles,
    emitter,
    pieceThemes: pieceStyles,
    className: Constants.CssClasses.Board,
    containerEl: el,
    methods,
    name: Renderer.Types.WebGL3D,
    options,
    testElement,
  };

  return createWebGLRendererInstance(params);
}
