import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { limitNumberWithinRange } from 'utils/limit-number-within-range';

/**
 * Get the nearest position from a square.
 */
export function positionToNearestSquare(
  state: WebGLRenderer3D.State,
  position: WebGLRenderer.BoardPosition,
): WebGLRenderer.BoardPosition {
  const { squareSize } = state;
  const tolerance = 0.01;
  const boardSize = squareSize * 8;

  return {
    x: limitNumberWithinRange(position.x, {
      min: tolerance,
      max: boardSize - tolerance,
    }),
    y: limitNumberWithinRange(position.y, {
      min: tolerance,
      max: boardSize - tolerance,
    }),
  };
}
