import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';
import { resetCamera } from 'chessboard/renderers/webgl/three-d/camera/reset-camera';

/**
 * On mouse double click.
 */
export function onMouseDoubleClick(state: WebGLRenderer3D.State): void {
  const { animation, cameraState, isPromotionWindowVisible, options, pointer } = state;

  if (!options.enabled || isPromotionWindowVisible || pointer.insideBoard) {
    return;
  }

  resetCamera({
    animation,
    isFlipped: options.flipped,
    state: cameraState,
  });
}
