import * as THREE from 'three';
import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Initialize the lights.
 */
export function createLights(state: WebGLRenderer3D.State): void {
  // top light
  state.lights.topLight = new THREE.PointLight();
  state.lights.topLight.intensity = 0.65;
  state.lights.topLight.position.set(Constants.Square.Size * 4, 150, Constants.Square.Size * 4);

  // white's side light
  state.lights.whiteSideLight = new THREE.DirectionalLight();
  state.lights.whiteSideLight.intensity = 0.45;
  state.lights.whiteSideLight.position.set(
    Constants.Square.Size * 4,
    100,
    Constants.Square.Size * 4 + 200,
  );
  (<THREE.DirectionalLight>state.lights.whiteSideLight).target.position.set(
    Constants.Square.Size * 4,
    0,
    Constants.Square.Size * 4,
  );

  // black's side light
  state.lights.blackSideLight = new THREE.DirectionalLight();
  state.lights.blackSideLight.intensity = 0.45;
  state.lights.blackSideLight.position.set(
    Constants.Square.Size * 4,
    100,
    Constants.Square.Size * 4 - 200,
  );
  (<THREE.DirectionalLight>state.lights.blackSideLight).target.position.set(
    Constants.Square.Size * 4,
    0,
    Constants.Square.Size * 4,
  );

  // light that will follow the camera position
  state.lights.movingLight = new THREE.PointLight(0xf9edc9);
  state.lights.movingLight.position.set(0, 10, 0);
  state.lights.movingLight.intensity = 0.25;
  (<THREE.PointLight>state.lights.movingLight).distance = 500;

  // add the lights in the scene
  state.scene.add(state.lights.topLight);
  state.scene.add(state.lights.whiteSideLight);
  state.scene.add(state.lights.blackSideLight);
  state.scene.add(state.lights.movingLight);
}
