import type { Board } from 'chessboard/model/board/types';
import type { WebGLRenderer } from 'chessboard/renderers/webgl/base';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Get the board position from file/rank coordinates.
 */
export function fileRankToPosition(
  state: WebGLRenderer3D.State,
  fileRank: Board.FileRank,
): WebGLRenderer.BoardPosition {
  const { squareSize } = state;

  return {
    x: squareSize * (fileRank.file - 1) + squareSize * 0.5,
    y: squareSize * (8 - fileRank.rank) + squareSize * 0.5,
  };
}
