import { Constants } from 'chessboard/renderers/webgl/three-d/constants';
import type { WebGLRenderer3D } from 'chessboard/renderers/webgl/three-d/types';

/**
 * Hide the hover square.
 */
export function hideHoverSquare(state: WebGLRenderer3D.State): void {
  const { hoverSquare } = state;

  if (!hoverSquare) {
    return;
  }

  hoverSquare.position.x = Constants.Square.Size;
  hoverSquare.position.y = Constants.Square.InvisibleY;
  hoverSquare.position.z = Constants.Square.Size;
}
